<template>
	<!-- 头部蓝色区域 -->
	<view class="top-box">
		<view class="user-box">
			<!-- 司机头像 -->
			<view class="avatar">
				<image src="../../static/img/avatar.png" mode="widthFix"></image>
			</view>
			<!-- 司机号码 -->
			<view v-if="userPhone !== 0" class="user-tel">
				<slot>{{userPhone}}</slot>
			</view>
		</view>
		<!-- 背景装饰圆 -->
		<view class="top-circle qrcode-circle1"></view>
		<view class="top-circle qrcode-circle2"></view>
		<view class="top-circle qrcode-circle3"></view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				userPhone: 0
			}
		},
		mounted() {
			// 获取用户手机号
			this.getPersonalMsg()
		},
		methods: {
			// 获取用户信息
			async getPersonalMsg() {
				const res = await this.$request({
					method: 'POST',
					url: 'system/personal'
				})
				if (res.success === false) {
					uni.showToast({
						title: res.message,
						icon: 'none'
					})
				} else if (res.success === true) {
					const { phone } = res.data
					this.userPhone = phone
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.top-box{
		position: relative;
		height: 310rpx;
		background-color: $color;
		padding: 80rpx 0;
		@include flex-center;
		overflow: hidden;
		.user-box{
			display: flex;
			flex-direction: column;
			align-items: center;
			.avatar{
				width: 130rpx;
				height: 130rpx;
				image{
					height: 100%;
				}
			}
			.user-tel{
				color: #FFFFFF;
				font-size: 26rpx;
				line-height: 1;
				margin-top: 20rpx;
			}
		}
		.top-circle{
			position: absolute;
			background: #FFFFFF;
			opacity: 0.06;
			border-radius: 50%;
		}
		.qrcode-circle1{
			width: 235rpx;
			height: 235rpx;
			left: -100rpx;
			bottom: -170rpx;
		}
		.qrcode-circle2{
			width: 391px;
			height: 391px;
			bottom: -530rpx;
			right: -330rpx;
		}
		.qrcode-circle3{
			width: 76rpx;
			height: 76rpx;
			bottom: 40rpx;
			right: 130rpx;
		}
	}
</style>
